import {useEffect, useMemo, useState} from 'react';
import {Alert, Image, Pressable, View} from 'react-native';
import TextCM from '../../../../components/Text';
import {Divider, Icon, List, ListItem} from '@ui-kitten/components';
import {Font, String} from '../../../../../constants';
import {IJob} from '../components/BackgroundFlow/interface';
import InfoBodyScreen from '../../../review-eid/components/Info';
import {DateUtils} from '../../../../../utils';
import {Iconhigh, Iconlow, Iconmedium} from '../../../../../assets/images';
import BottomSheetCM from '../../../../components/BottomSheet';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
import {IAccount} from '../../../../../model/interface/account.interface';

type Props = {
  dataTask: IJob;
};

const TaskItem = ({dataTask}: Props) => {
  const [showMoreData, setShowMoreData] = useState(false);
  const [showMoreUser, setShowMoreUser] = useState(false);
  const [showMoreOwner, setShowMoreOwner] = useState(false);
  const [listUserOwner, setListUserOwner] = useState<IAccount[]>([]);

  const TicketServices = useTicketServices();

  useEffect(() => {
    getListUserOwner();
  }, []);

  const getListUserOwner = async () => {
    const listEmail = dataTask.owner?.split(',');
    if (listEmail) {
      const data = await TicketServices.getMultiLoginAccount(listEmail);
      if (data) {
        setListUserOwner(data);
      }
    }
  };

  const toogleMoreUser = () => setShowMoreUser(!showMoreUser);
  const toogleMoreOwner = () => setShowMoreOwner(!showMoreOwner);

  const getStatusType = (task: IJob) => {
    if (task?.attrJson?.type === 'todo') {
      return 'todo';
    } else if (task?.attrJson?.type === 'processing') {
      return 'processing';
    } else if (task?.attrJson?.type === 'completed') {
      return 'completed';
    } else if (task?.attrJson?.type === 'approved') {
      return 'approved';
    } else {
      return task.statusType;
    }
  };

  const getTimeEstimate = (startDate: string, dueDate: string) => {
    if (startDate && dueDate) {
      const endTime = Math.floor(new Date(dueDate).getTime());
      const currentTime = Math.floor(new Date(startDate).getTime());
      const time = endTime - currentTime;
      return DateUtils.convertTime(time);
    } else {
      return '---';
    }
  };

  const getPriority = (priority: string) => {
    switch (priority) {
      case '3':
        return <Iconhigh width={16} height={16} />;
      case '2':
        return <Iconmedium width={16} height={16} />;
      case '1':
        return <Iconlow width={16} height={16} />;
      default:
        return <TextCM>---</TextCM>;
    }
  };

  const getValueAssnee = (data: any, isAssnee = true) => {
    return (
      <Pressable
        onPress={isAssnee ? toogleMoreUser : toogleMoreOwner}
        style={{
          flexDirection: 'row',
          position: 'relative',
        }}>
        {data?.map((user: any, index: any) => (
          <>
            {index <= 1 ? (
              <Image
                key={index}
                style={{position: 'absolute', left: index * 20}}
                width={28}
                height={28}
                borderRadius={14}
                source={{
                  uri:
                    user?.[isAssnee ? 'avatar' : 'imageUrl'] ||
                    String.DEFAULT_IMAGE,
                }}
              />
            ) : (
              <View
                key={index}
                style={{
                  position: 'absolute',
                  left: 2 * 20,
                  width: 28,
                  height: 28,
                  borderRadius: 14,
                  backgroundColor: '#F4F4F4',
                  justifyContent: 'center',
                  alignItems: 'center',
                }}>
                <TextCM>{`+${data?.length - 2}`}</TextCM>
              </View>
            )}
          </>
        ))}
      </Pressable>
    );
  };

  const dataInfo: any[] = useMemo(() => {
    return [
      {
        key: 'Ch s hu',
        type: 'normal',
        value: {
          onlyIcon: getValueAssnee(listUserOwner, false),
        },
      },
      {
        key: 'Ngi thc hin',
        type: 'normal',
        value: {
          onlyIcon: getValueAssnee(dataTask.assignTaskList),
        },
      },
      {
        key: 'RV.status',
        type: 'status',
        value: {
          status: getStatusType(dataTask),
        },
      },
      {
        key: 'Estimate',
        type: 'normal',
        value: {
          name: getTimeEstimate(dataTask.startDate, dataTask.dueDate),
        },
      },
      {
        key: ' u tin',
        type: 'normal',
        value: {
          onlyIcon: getPriority(dataTask.priority),
        },
      },
      {
        key: 'Ngy bt u d kin',
        type: 'normal',
        value: {
          name: DateUtils.convertDateToDDMMYYYY_H_M(dataTask.startDate),
        },
      },
      {
        key: 'Ngy kt thc d kin',
        type: 'normal',
        value: {
          name: DateUtils.convertDateToDDMMYYYY_H_M(dataTask.dueDate),
        },
      },
      {
        key: 'Hnh ng',
        type: 'normal',
        value: {
          onlyIcon: (
            <Pressable
              onPress={() => Alert.alert('Tnh nng ang pht trin')}
              style={{
                width: 28,
                height: 28,
                backgroundColor: '#F2ECFD',
                borderRadius: 14,
                justifyContent: 'center',
                alignItems: 'center',
              }}>
              <Icon fill={'#6F2CED'} name="edit-2" width={16} height={16} />
            </Pressable>
          ),
        },
      },
    ];
  }, [listUserOwner]);

  const toggleShowMoreData = () => {
    setShowMoreData(!showMoreData);
  };

  return (
    <View style={{paddingHorizontal: 10}}>
      <Pressable
        onPress={toggleShowMoreData}
        style={{
          backgroundColor: '#fff',
          flexDirection: 'row',
          alignItems: 'flex-start',
          justifyContent: 'space-between',
          columnGap: 30,
        }}>
        <View
          style={{flexDirection: 'row', alignItems: 'center', columnGap: 8}}>
          <View
            style={[
              showMoreData && {
                transform: [{rotate: '90deg'}],
              },
            ]}>
            <Icon
              fill={'#6F2CED'}
              width={16}
              height={16}
              name="play-circle-outline"
            />
          </View>
          <TextCM
            style={{
              color: '#00204DE5',
              fontSize: 12,
              fontFamily: Font.InterSemiBold600,
            }}>
            Tiu 
          </TextCM>
        </View>
        <TextCM
          style={{
            flex: 1,
            fontSize: 12,
            color: '#366AE2',
            fontFamily: Font.InterRegular400,
          }}>
          {dataTask.name}
        </TextCM>
      </Pressable>

      {showMoreData && (
        <View style={{backgroundColor: '#fff', paddingHorizontal: 16}}>
          <InfoBodyScreen dataInfo={dataInfo} />
        </View>
      )}
      <Divider style={{marginTop: 10}} />

      <BottomSheetCM
        isVisible={showMoreUser}
        title="Danh sch ngi thc hin"
        onClose={toogleMoreUser}
        renderContent={
          <List
            data={dataTask.assignTaskList}
            renderItem={({item}) => (
              <ListItem
                children={
                  <View
                    style={{
                      flexDirection: 'row',
                      alignItems: 'center',
                      justifyContent: 'center',
                      columnGap: 16,
                    }}>
                    <Image
                      width={28}
                      height={28}
                      borderRadius={14}
                      source={{uri: item.avatar || String.DEFAULT_IMAGE}}
                    />
                    <View>
                      <TextCM
                        style={{
                          fontSize: 14,
                          fontFamily: Font.InterSemiBold600,
                        }}>
                        {item.name}
                      </TextCM>
                      <TextCM>{item.email}</TextCM>
                    </View>
                  </View>
                }
              />
            )}
          />
        }
      />

      <BottomSheetCM
        isVisible={showMoreOwner}
        title="Danh sch ch s hu"
        onClose={toogleMoreOwner}
        renderContent={
          <List
            data={listUserOwner}
            renderItem={({item}) => (
              <ListItem
                children={
                  <View
                    style={{
                      flexDirection: 'row',
                      alignItems: 'center',
                      justifyContent: 'center',
                      columnGap: 16,
                    }}>
                    <Image
                      width={28}
                      height={28}
                      borderRadius={14}
                      source={{uri: item.imageUrl || String.DEFAULT_IMAGE}}
                    />
                    <View>
                      <TextCM
                        style={{
                          fontSize: 14,
                          fontFamily: Font.InterSemiBold600,
                        }}>
                        {item.firstName + ' ' + item.lastName || '---'}
                      </TextCM>
                      <TextCM>{item.email}</TextCM>
                    </View>
                  </View>
                }
              />
            )}
          />
        }
      />
    </View>
  );
};

export default TaskItem;
